/*
Normalize WordPress admin for Ninja Forms App
---------------------------------------------*/
html {
  overflow: hidden;
  width: 100%;
  float: left;
  left: 0;
}

#wp-auth-check-wrap #wp-auth-check {
  z-index: 1000000000000;
}

.ninja-forms-app {
  height: 0;
}
.ninja-forms-app #adminmenumain,
.ninja-forms-app #wpfooter {
  display: none;
}
.ninja-forms-app #wpwrap {
  height: 0;
  min-height: 0;
}
.ninja-forms-app #wpcontent #wpbody {
  font-size: 16px;
}
.ninja-forms-app #wpcontent * {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}
.ninja-forms-app #wpcontent .wp-editor-tabs button {
  box-sizing: initial;
}
.ninja-forms-app .grey #nf-drawer .nf-button {
  padding: 15px 30px;
}

.media-modal {
  z-index: 100000000;
}

#nf-builder {
  background: #fff;
  padding: 30px 40px;
  overflow: hidden;
  padding-top: 80px;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 10000000;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.7);
}

.product-content article > *:not(img) {
  padding-left: 30px;
  padding-right: 30px;
}

/*
Header and Nav for Ninja Forms App
---------------------------------- */
#nf-header {
  height: 100px;
  transition: margin 0.5s ease;
}
#nf-header #nf-logo {
  background-image: url("../img/ninja-forms-app-header-logo.png");
  float: left;
  width: 80px;
  height: 30px;
  opacity: 1;
  filter: alpha(opacity=100);
  transition: all 1s ease;
}
@media (min-resolution: 192dpi), (-webkit-min-device-pixel-ratio: 2), (min--moz-device-pixel-ratio: 2), (-o-min-device-pixel-ratio: 2/1), (min-device-pixel-ratio: 2), (min-resolution: 2dppx) {
  #nf-header #nf-logo {
    background-image: url("../img/ninja-forms-app-header-logo@2x.png");
    background-size: 80px 30px;
  }
}
#nf-header ul {
  display: inline-block;
  margin: 0;
}
#nf-header ul li {
  float: left;
}
#nf-header ul li.nf-mobile-menu {
  display: none;
}
#nf-header ul li a {
  color: #424242;
  display: block;
  margin: 16px 20px;
  padding-bottom: 8px;
  text-decoration: none;
}
#nf-header ul li a.active {
  border-bottom: 2px solid #424242;
}
#nf-header ul li a.preview {
  color: #bbb;
  transition: all 1s ease;
}
#nf-header ul li a.preview .dashicons {
  display: inline-block;
  margin-left: 10px;
}
#nf-header ul li:first-child a {
  margin-left: 0;
}
#nf-header ul:after {
  clear: both;
  content: "";
  display: block;
}
#nf-header input[type=submit] {
  float: right;
}
#nf-header .nf-cancel {
  float: right;
  margin-right: 60px;
  padding: 8px 0;
}
#nf-header .nf-cancel .dashicons {
  font-size: 30px;
}
#nf-header .nf-public-link {
  float: right;
  margin-right: 30px;
  padding: 8px 0;
}
#nf-header .nf-public-link .dashicons {
  font-size: 30px;
}
#nf-header:after {
  clear: both;
  content: "";
  display: block;
}

#nf-app-header {
  height: 50px;
}
#nf-app-header:after {
  clear: both;
  content: "";
  display: block;
}

#nf-app-sub-header {
  height: 50px;
  padding: 0;
  margin-top: 20px;
}
#nf-app-sub-header h2 {
  display: inline-block;
  margin: 0;
  padding: 10px 0;
  transition: all 1s ease 0s;
}
#nf-app-sub-header:after {
  clear: both;
  content: "";
  display: block;
}

.nf-app-buttons {
  float: right;
}
.nf-app-buttons .publish {
  float: right;
}

#nf-app-form-title {
  min-height: 18px;
}
#nf-app-form-title h2 {
  margin: 15px 0;
}

#nf-main {
  border: 4px solid #fff;
  width: 100%;
  position: absolute;
  top: 220px;
  overflow: auto;
  overflow-x: visible;
  bottom: 0;
  left: 0;
  -webkit-overflow-scrolling: touch;
  transition: left 0.5s ease;
}
#nf-main .nf-field-type-button {
  width: 400px;
}

.nf-main-test {
  width: 100%;
  transition: width 0.5s ease;
}

#nf-main-content,
#nf-main-header {
  margin: 0 auto;
  max-width: 860px;
  user-select: none;
  padding: 0 80px;
  position: relative;
}

#nf-main-header .nf-button {
  float: right;
}
#nf-main-header h2 {
  display: inline-block;
}
#nf-main-header:after {
  clear: both;
  content: "";
  display: block;
}

#the-list tr.flagged th:first-child {
  border-left: 5px solid yellow;
}

#nf-main-gutter-left,
#nf-main-gutter-right {
  position: absolute;
  top: 0;
  width: 80px;
  height: 450px;
}
#nf-main-gutter-left .fa-chevron-circle-left,
#nf-main-gutter-left .fa-chevron-circle-right,
#nf-main-gutter-left .fa-plus-circle,
#nf-main-gutter-right .fa-chevron-circle-left,
#nf-main-gutter-right .fa-chevron-circle-right,
#nf-main-gutter-right .fa-plus-circle {
  color: #eee;
  font-size: 86px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}
#nf-main-gutter-left .fa-chevron-circle-left:hover,
#nf-main-gutter-left .fa-chevron-circle-right:hover,
#nf-main-gutter-left .fa-plus-circle:hover,
#nf-main-gutter-right .fa-chevron-circle-left:hover,
#nf-main-gutter-right .fa-chevron-circle-right:hover,
#nf-main-gutter-right .fa-plus-circle:hover {
  color: #1EA9EA;
  cursor: pointer;
}

.nf-realistic-field .nf-realistic-field-mimic {
  border-radius: 4px;
}

.nf-field-wrap.date .nf-realistic-field .nf-realistic-field-mimic {
  display: inline-block;
  padding-right: 1px;
}

.nf-field-wrap.date .nf-realistic-field input + .nf-realistic-field-mimic {
  margin-top: 5px;
}

#nf-main-gutter-left {
  left: 0;
  padding-left: 6px;
}

#nf-main-gutter-right {
  right: 0;
  padding-right: 6px;
}

.nf-fields-empty {
  padding: 0 40px;
}
.nf-fields-empty h3 {
  font-size: 30px;
  line-height: 32px;
}
.nf-fields-empty p {
  font-size: 18px;
}
.nf-fields-empty a {
  text-decoration: none;
}

.nf-field-label {
  display: inline-block;
  margin-left: 16px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  width: 80%;
}
.nf-drawer-opened .nf-field-label {
  width: 60%;
}

.template-box {
  text-decoration: none;
}
.template-box .template-box-inside {
  background: #EBEDEE;
  color: #424242;
  min-height: 160px;
  padding: 10px;
  text-decoration: none;
  transition: all 0.2s ease-in-out 0.1s, box-shadow 0.2s ease-in-out 0.1s;
}
.template-box .template-box-inside h4 {
  margin: 0 0 10px;
  min-height: 35px;
}
.template-box .template-box-inside p {
  font-size: 12px;
  margin: 0;
}
.template-box:hover .template-box-inside {
  background: #fff;
  box-shadow: 0 0 20px #1EA9EA;
  color: #1EA9EA;
  transition: all 0.2s ease-in-out 0.1s, box-shadow 0.2s ease-in-out 0.1s;
}
.nf-drawer-opened .template-box {
  width: 50%;
}

@media only screen and (max-width: 1400px) {
  .nf-drawer-opened .template-box {
    width: 100%;
  }
}
.nf-setting-wrap {
  cursor: pointer;
  float: left;
  margin-bottom: 30px;
  padding: 0 2%;
  width: 33.33%;
}
.nf-setting-wrap::after {
  clear: both;
  content: "";
  display: block;
}
.nf-setting-wrap span {
  background: #fff;
  border: 1px solid #ccc;
  border-radius: 4px;
  display: table-cell;
  height: 85px;
  width: 400px;
  text-align: center;
  vertical-align: middle;
}
.nf-setting-wrap.active span {
  border: 2px solid #84CC1E;
  font-weight: bold;
}

.available .nf-setting-wrap span {
  background: #ebedee;
}

/*
Form field placeholders for the fields a user has added to their form
----------------------------------------------------------------------- */
.nf-item-controls {
  height: 30px;
  width: 30px;
  float: right;
  margin: 0;
  position: absolute;
  top: 9px;
  right: 10px;
}
.nf-item-controls .nf-item-control {
  visibility: hidden;
  margin: 0;
  opacity: 0;
  text-align: center;
  width: 40px;
  height: 30px;
  position: absolute;
  right: 0;
  transition: all 0.5s ease;
}
.nf-item-controls .nf-item-control a {
  background: #fff;
  border: 1px solid #fff;
  border-radius: 50px;
  color: #1EA9EA;
  display: block;
  text-decoration: none;
  padding: 3px;
  position: relative;
  width: 32px;
}
.nf-item-controls .nf-item-control a:hover {
  background: #fff;
  border: 1px solid #1EA9EA;
  color: #1EA9EA;
}
.nf-item-controls .nf-item-control .fa {
  font-size: 18px;
  line-height: 24px;
  text-align: center;
}
.nf-item-controls .nf-item-control.nf-item-edit {
  opacity: 100;
  visibility: visible;
  z-index: auto;
}
.nf-item-controls:hover a {
  background: #1EA9EA;
  color: #fff;
}
.nf-item-controls:hover .nf-item-duplicate {
  visibility: visible;
  opacity: 100;
  right: 80px;
  transition: all 0.5s ease;
}
.nf-item-controls:hover .nf-item-delete {
  visibility: visible;
  opacity: 100;
  right: 40px;
  transition: all 0.5s ease;
}

#nf-table-display .nf-item-controls {
  float: right;
}

.repeater .nf-item-duplicate {
  display: none;
}

.nf-realistic-field {
  font-size: 18px !important;
}

.nf-field-label {
  overflow: visible !important;
}

.nf-realistic-field input[type=text],
.nf-realistic-field input[type=email],
.nf-realistic-field select {
  width: 100%;
  max-width: 100%;
  background: #f9f9f9;
  height: 50px !important;
  padding: 12px;
  border: 0;
  color: #787878;
}

.nf-realistic-field select[multiple] {
  height: auto !important;
}

.ninja-forms-req-symbol {
  color: red;
}

.nf-realistic-field button,
.nf-realistic-field input[type=button],
.nf-realistic-field input[type=submit] {
  width: auto;
  height: 50px;
  padding: 12px;
  border-radius: 2px;
  background: #333;
  border: 0;
  color: #f7f7f7;
}

.nf-realistic-field input[type=checkbox],
.nf-realistic-field input[type=radio] {
  height: 20px;
  width: 20px;
}

.nf-realistic-field .nf-realistic-field--element input[type=radio] {
  border-width: 2px !important;
