}

.nf-group-wrap {
  background: #fff;
  border: 2px solid #ccc;
  border-radius: 4px;
  color: #888;
  margin: 0 -10px 20px;
  padding: 15px 30px;
  position: relative;
}
.nf-group-wrap .nf-item-controls {
  width: 128px;
}
.nf-group-wrap .nf-item-expand {
