@media only screen and (max-width: 660px) {
  .nf-one-half, .nf-one-third {
    width: 100%;
  }
  .nf-actions-table td:nth-child(3), .nf-actions-table th:nth-child(3) {
    display: none;
  }
  .nf-drawer-buttons {
    display: block;
  }
  .nf-item {
    padding: 15px;
  }
  .nf-item .fa {
    padding: 14px;
    width: 48px;
  }
  .nf-saved .nf-item::after {
    padding: 14px;
  }
}
#nf-app-admin-header {
  background: #EBEDEE;
  height: 60px;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}
#nf-app-admin-header .fa-times {
  background: #424242;
  color: #fff;
  font-size: 30px;
  position: absolute;
  text-align: center;
  text-decoration: none;
  line-height: 56px;
  right: 0;
  height: 60px;
  width: 60px;
}
#nf-app-admin-header #nf-logo {
  background-image: url("../img/ninja-forms-app-header-logo@2x.png");
  background-size: 130px 50px;
  background-position: center bottom;
  background-repeat: no-repeat;
  float: left;
  width: 160px;
  height: 60px;
  opacity: 1;
  filter: alpha(opacity=100);
  -webkit-transition: all 1s ease;
  transition: all 1s ease;
}
#nf-app-admin-header .nf-cta-bubble {
  background: #1EA9EA;
  border: 1px solid #1EA9EA;
  border-radius: 6px;
  color: #fff;
  display: block;
  float: left;
  font-size: 14px;
  margin: 6px 0 0 20px;
  padding: 5px 10px;
  position: relative;
  text-decoration: none;
  width: 360px;
}
#nf-app-admin-header .nf-cta-bubble::after {
  display: block;
  content: "";
  width: 0;
  height: 0;
  border-top: 10px solid transparent;
  border-bottom: 10px solid transparent;
  border-right: 10px solid #1EA9EA;
  position: absolute;
  left: -10px;
  top: 13px;
}
#nf-app-admin-header .nf-cta-bubble:hover {
  background: #fff;
  color: #1EA9EA;
}

.scrollWrap {
  overflow: hidden;
  height: 100%;
  position: relative;
  z-index: 1;
}

.scrollContent {
  height: 100%;
  width: 100%;
  padding: 0 32px 0 0;
  position: relative;
  right: -18px;
  overflow: auto;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

.fakeScrollBar {
  position: absolute;
  background: rgba(255, 255, 255, 0.5);
  width: 9px;
  border-radius: 4px;
  right: -15px;
  top: 0;
  z-index: 0;
  cursor: -moz-grab;
  cursor: -webkit-grab;
}

.fakeScroll:hover .fakeScrollBar.fakescroll-grabbed {
  cursor: -moz-grabbing;
  cursor: -webkit-grabbing;
  background: #FFF;
}

body.fakescroll-grabbed {
  cursor: -moz-grabbing;
  cursor: -webkit-grabbing;
}

